import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Particles from 'react-particles-js';

import Header from './header';
import '../styles/layout.scss';
import defaultConfig from '../components/particles-config';

import { library, config } from '@fortawesome/fontawesome-svg-core';
import {
  faGithub,
  faLinkedin,
  faTwitter,
} from '@fortawesome/free-brands-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

library.add(faGithub);
library.add(faLinkedin);
library.add(faTwitter);
config.autoAddCss = false;

class Layout extends React.Component {
  render() {
    return (
      <StaticQuery
        query={graphql`
          query SiteTitleQuery {
            site {
              siteMetadata {
                title
                navLinks {
                  name
                  link
                }
                social {
                  name
                  link
                }
              }
            }
          }
        `}
        render={data => (
          <>
            <Header
              siteTitle={data.site.siteMetadata.title}
              navLinks={data.site.siteMetadata.navLinks}
            />
            <div>
              <div
                style={{
                  position: 'relative',
                  backgroundColor: '#161616',
                  width: '100%',
                  height: this.props.bannerHeight,
                }}
              >
                {this.props.banner}
                <Particles
                  className="particles"
                  params={{
                    particles: {
                      number: {
                        value: 150,
                        density: {
                          enable: true,
                          value_area: 2000,
                        },
                      },
                      color: {
                        value: '#fb713f',
                      },
                      shape: {
                        type: 'circle',
                        stroke: {
                          width: 0,
                          color: '#000000',
                        },
                        polygon: {
                          nb_sides: 5,
                        },
                        image: {
                          src: 'img/github.svg',
                          width: 100,
                          height: 100,
                        },
                      },
                      opacity: {
                        value: 0.5,
                        random: false,
                        anim: {
                          enable: false,
                          speed: 1,
                          opacity_min: 0.1,
                          sync: false,
                        },
                      },
                      size: {
                        value: 3,
                        random: false,
                        anim: {
                          enable: false,
                          speed: 40,
                          size_min: 0.1,
                          sync: false,
                        },
                      },
                      line_linked: {
                        enable: true,
                        distance: 150,
                        color: '#fb713f',
                        opacity: 0.4,
                        width: 1,
                      },
                      move: {
                        enable: true,
                        speed: 1,
                        direction: 'none',
                        random: false,
                        straight: false,
                        out_mode: 'out',
                        bounce: false,
                        attract: {
                          enable: false,
                          rotateX: 600,
                          rotateY: 1200,
                        },
                      },
                    },
                    interactivity: {
                      detect_on: 'canvas',
                      events: {
                        onhover: {
                          enable: false,
                          mode: 'repulse',
                        },
                        onclick: {
                          enable: false,
                          mode: 'push',
                        },
                        resize: true,
                      },
                      modes: {
                        grab: {
                          distance: 400,
                          line_linked: {
                            opacity: 1,
                          },
                        },
                        bubble: {
                          distance: 400,
                          size: 40,
                          duration: 2,
                          opacity: 8,
                          speed: 3,
                        },
                        repulse: {
                          distance: 200,
                          duration: 0.4,
                        },
                        push: {
                          particles_nb: 4,
                        },
                        remove: {
                          particles_nb: 2,
                        },
                      },
                    },
                    retina_detect: true,
                  }}
                />
              </div>
              <main>{this.props.children}</main>
              <footer>
                <div className="footer-wrapper">
                  <div className="media-icons-wrapper">
                    {data.site.siteMetadata.social.map(({ name, link }) => (
                      <a
                        className="media-icon"
                        key={`media-icon-${name}`}
                        href={link}
                      >
                        <FontAwesomeIcon width={48} icon={['fab', name]} />
                      </a>
                    ))}
                  </div>
                  <div className="footer-other">
                    <p>liamdalg99@gmail.com</p>
                    <p>
                      © 2019 Liam Dalgarno | Made with GatsbyJS and Github
                      Pages.
                    </p>
                  </div>
                </div>
              </footer>
            </div>
          </>
        )}
      />
    );
  }
}

export default Layout;
